<template>
  <div class="container">
    <section>
      <!-- <button @click.once="createHanger" disabled>创建衣架</button>
      <span class="split-line">|</span>
      <button @click.once="createPath" disabled>创建轨道</button>
      <span class="split-line">|</span>
      <input type="number" placeholder="工位wip" v-model="stationWip" disabled />
      <button @click.once="createStation" disabled>创建工位</button>
      <span class="split-line">|</span>-->
      <button
        v-for="(item,index) in stationArray"
        :key="'push'+index+`${item.key}`"
        @click="hangerIn(index)"
      >衣架入栈 -> {{item.name}}</button>
      <span class="split-line">|</span>
      <button
        v-for="(item,index) in stationArray"
        :key="'out'+index+`${item.key}`"
        @click="hangerOut(index)"
      >{{item.name}} -> 衣架出栈</button>
      <span class="split-line">|</span>
      <button
        v-for="(item,index) in stationArray"
        :key="'spin'+index+`${item.key}`"
        @click="rotateSvg(index)"
      >旋转工位 -> {{item.name}}</button>
    </section>
    <div class="svgcontainer" id="svgcontainer"></div>
    <div class="data-show">
      <div class="pool-data">
        订单池数据:
        <p v-for="(item,index) in poolArray" :key="index">
          {
          <span>id: {{item.id}} ,</span>
          <span>
            color:
            <i :style="{background:item.color}"></i> ,
          </span>
          <span>position: {{item.position}}</span>
          }
        </p>
      </div>
      <div class="station-data">
        工位数据:
        <div class="station-data-detail">
          <div class="detail" v-for="(items,indexs) in stationOrdersArray" :key="indexs">
            {{stationArray[indexs].name}}:
            <p v-for="(item,index) in items" :key="index">
              <template v-if="item">
                {
                <span>id: {{item.id}} ,</span>
                <span>
                  color:
                  <i :style="{background:item.color}"></i> ,
                </span>
                <span>position: {{item.position}}</span>
                }
              </template>
              <template v-else>{{item}}</template>
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="log">
      数据源:
      <p v-for="(item,index) in clothingData" :key="index">
        {
        <span>id: {{item.id}} ,</span>
        <span>
          color:
          <i :style="{background:item.color}"></i> ,
        </span>
        <span>position: {{item.position}}</span>
        }
      </p>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import _ from "lodash";

export default {
  data() {
    return {
      poolArray: [],
      stationArray: [],
      svg: null,
      lineGroup: null,
      lineGroupArray: [],
      pathArray: [],
      hangerPool: null,
      stationGroupArray: [],
      stationOrders: [],
      stationOrdersArray: [],
      currentElement: null,
      clothingData: [],
      rotateFlag: true,
      stationWip: 4,
      stationPosition: [],
      dataSourcePosition: []
    };
  },
  computed: {
    ...mapState(["plotData"])
  },
  mounted() {
    this.svgInit();
  },
  methods: {
    formatData(data) {
      const nodeDataArray = data.nodeDataArray;
      const dataSourceArray = [];
      const stationArray = [];
      const linkDataArray = data.linkDataArray;
      const linkAmount = linkDataArray.length;
      nodeDataArray.forEach((item, index) => {
        switch (item.type) {
          case "dataSource":
            if (index == 0) dataSourceArray.push(item);
            break;
          case "station":
            stationArray.push(item);
            break;
        }
      });
      dataSourceArray.forEach((item, index) => {
        const dataSourceOptions = { name: item.name };
        this.createHanger(dataSourceOptions);
      });
      stationArray.forEach((item, index) => {
        this.stationWip = item.wip;
        const stationOptions = {
          angle: item.angle ? item.angle : 0,
          name: item.name,
          wipValue: item.wip,
          position: index
        };
        this.createStation(stationOptions);
      });
      this.stationArray = stationArray;

      linkDataArray.forEach((item, index) => {
        const linkOptions = {
          position: index
        };
        this.createPath(linkOptions);
      });
    },
    svgInit() {
      const width = `80%`;
      const height = `100%`;
      //Create SVG element
      this.svg = this.$d3
        .select("#svgcontainer")
        .append("svg")
        .attr("width", width)
        .attr("height", height);
      if (this.plotData) this.formatData(this.plotData);
    },
    createPath({ position }) {
      let that = this;
      // 插入基本组件轨道示例;
      let lineGroup = that.svg
        .append("g")
        .attr("transform", "translate(250,70)");
      // lineGroup
      //   .append("line")
      //   .attr("x1", 0)
      //   .attr("y1", 50)
      //   .attr("x2", 100)
      //   .attr("y2", 50)
      //   .style("stroke", "#80b858")
      //   .style("stroke-width", 2);

      var line = that.$d3.line().curve(that.$d3.curveStep);

      var lineData = [
        [0, 0],
        [200, (100 + 10) * position]
        // [100, 100],
        // [100, 150],
        // [50, 100]
      ];

      let tempLine = lineGroup
        .append("path")
        .attr("d", line(lineData))
        .style("stroke", "black")
        .style("fill", "none");

      that.pathArray.push(tempLine);
      that.lineGroupArray.push(lineGroup);
    },
    pathTransition(position) {
      let clothingData = [this.currentElement];

      let that = this;

      let rect = that.lineGroupArray[position]
        .selectAll(".rect")
        .data(clothingData)
        .enter()
        .append("rect");

      rect
        .attr("id", function(d) {
          return d.id;
        })
        .attr("x", 0)
        .attr("y", -30)
        .attr("width", 30)
        .attr("height", 60)
        .attr("fill", function(d) {
          return d.color;
        })
        .transition()
        .delay(250)
        .duration(1000)
        .ease(that.$d3.easeLinear)
        .tween("pathTween", () => {
          return pathTween(that.pathArray[position]);
        })
        .remove()
        .on("end", () => {
          console.log(`结束`);
          this.enterStation(position);
        });

      function pathTween(path) {
        var length = path.node().getTotalLength(); // Get the length of the path
        var r = that.$d3.interpolate(0, length); //Set up interpolation from 0 to the path length
        return function(t) {
          var point = path.node().getPointAtLength(r(t)); // Get the next point along the path
          that.$d3
            .select(this) // Select the circle
            .attr("x", point.x) // Set the cx
            .attr("y", point.y - 30); // Set the cy
        };
      }
    },
    enterStation(position) {
      const currentStationOrders = this.stationOrdersArray[position];
      this.currentElement.position = this.stationArray[position].name;
      let indexNum = _.findLastIndex(currentStationOrders, element => {
        return element == 0;
      });
      // let indexNum = this.stationOrders.findIndex(
      //   element => element == 0
      // );
      currentStationOrders[indexNum] = this.currentElement;
      // if (indexNum != -1) {
      //   this.stationOrders[indexNum] = this.currentElement;
      // } else {
      //   this.stationOrders.push(this.currentElement);
      // }
      let clothingData = [this.currentElement];
      let rect = this.stationGroupArray[position]
        .selectAll(".rect")
        .data(clothingData)
        .enter()
        .append("rect");

      rect
        .attr("id", function(d) {
          return d.id;
        })
        .attr("x", 0)
        .attr("y", 20)
        .attr("width", 30)
        .attr("height", 60)
        .attr("fill", function(d) {
          return d.color;
        })
        .on("click", this.setCurryRect);
      let positionIndex = indexNum !== -1 ? indexNum : len;
      rect
        .transition()
        .duration(1000)
        .attr("x", `${100 + positionIndex * 30 + 30}`)
        .transition()
        .duration(1000)
        .attr("y", "0")
        .on("end", () => {
          this.currentElement = null;
        });
    },
    createStation({ angle, name, position, wipValue }) {
      const wip = parseInt(wipValue);
      if (!wip) {
        alert("请输入正确的wip");
        return;
      }
      // if (this.stationGroup) this.stationGroup.remove();
      //插入基本组件工位示例
      let stationGroup = this.svg
        .append("g")
        .attr("transform", `translate(450,${(100 + 10) * position + 20})`);

      //插入进站线路
      stationGroup
        .append("line")
        .attr("x1", 0)
        .attr("y1", 50)
        .attr("x2", 100)
        .attr("y2", 50)
        .style("stroke", "#5387cf")
        .style("stroke-width", 2);

      //插入wip
      let wipGroup = stationGroup
        .append("g")
        .attr("transform", "translate(100,0)");

      wipGroup
        .append("rect")
        .attr("x", 0)
        .attr("y", 0)
        .attr("width", (2 + wip) * 30)
        .attr("height", 100)
        .attr("fill", "none")
        .style("stroke", "#1c1c1c")
        .style("stroke-width", 2);

      let tempArray = [];
      for (let i = 0; i < wip; i++) {
        wipGroup
          .append("rect")
          .attr("x", i * 30 + 30)
          .attr("y", 0)
          .attr("width", 30)
          .attr("height", 60)
          .attr("fill", "none")
          .style("stroke", "#1c1c1c")
          .style("stroke-width", 2);
        tempArray.push(0);
      }
      this.stationOrdersArray.push(tempArray);

      //插入文本
      wipGroup
        .append("text")
        .attr("x", 5)
        .attr("y", 95)
        .attr("font-size", 16)
        .attr("fill", "black")
        .text(`name:${name}, wip:${wip}`);

      //插入出站线路
      stationGroup
        .append("line")
        .attr("x1", (2 + wip) * 30 + 100)
        .attr("y1", 50)
        .attr("x2", (2 + wip) * 30 + 200)
        .attr("y2", 50)
        .style("stroke", "#5387cf")
        .style("stroke-width", 2);

      //旋转工位
      if (angle) {
        stationGroup
          .transition()
          .duration(1000)
          .attr("transform", `translate(476,26) rotate(${angle})`);
      }

      this.stationPosition.push({
        origin: [450, (100 + 10) * position + 20],
        container: {
          width: (2 + wip) * 30,
          height: 100
        },
        pushLine: {
          start: [0, 50],
          end: [100, 50]
        },
        outLine: {
          start: [(2 + wip) * 30 + 100, 50],
          end: [(2 + wip) * 30 + 200, 50]
        }
      });

      this.stationGroupArray.push(stationGroup);
    },
    createHanger({ name }) {
      //插入衣架池
      this.hangerPool = this.svg
        .append("g")
        .attr("transform", "translate(20,20)");
      this.hangerPool
        .append("rect")
        .attr("x", 0)
        .attr("y", 0)
        .attr("width", 230)
        .attr("height", 100)
        .attr("fill", "none")
        .style("stroke", "#1c1c1c")
        .style("stroke-width", 2);
      let tempData = [
        {
          id: "clothing1",
          position: "订单池",
          color:
            "#" +
            ("00000" + ((Math.random() * 0x1000000) << 0).toString(16)).substr(
              -6
            )
        },
        {
          id: "clothing2",
          position: "订单池",
          color:
            "#" +
            ("00000" + ((Math.random() * 0x1000000) << 0).toString(16)).substr(
              -6
            )
        },
        {
          id: "clothing3",
          position: "订单池",
          color:
            "#" +
            ("00000" + ((Math.random() * 0x1000000) << 0).toString(16)).substr(
              -6
            )
        },
        {
          id: "clothing4",
          position: "订单池",
          color:
            "#" +
            ("00000" + ((Math.random() * 0x1000000) << 0).toString(16)).substr(
              -6
            )
        },
        {
          id: "clothing5",
          position: "订单池",
          color:
            "#" +
            ("00000" + ((Math.random() * 0x1000000) << 0).toString(16)).substr(
              -6
            )
        }
      ];
      this.poolArray = [...tempData];
      this.clothingData = [...tempData];
      let rects = this.hangerPool
        .selectAll(".rect")
        .data(this.clothingData)
        .enter()
        .append("rect");
      rects
        .attr("id", function(d) {
          return d.id;
        })
        .attr("x", function(d, i) {
          return i * 30 + 30;
        })
        .attr("y", 0)
        .attr("width", 30)
        .attr("height", 60)
        .attr("fill", function(d) {
          return d.color;
        })
        .on("click", this.setCurryRect);

      //插入文本
      this.hangerPool
        .append("text")
        .attr("x", 5)
        .attr("y", 95)
        .attr("font-size", 16)
        .attr("fill", "black")
        .text(name);

      this.dataSourcePosition.push({
        origin: [20, 20],
        container: {
          width: 230,
          height: 100
        }
      });
    },
    hangerIn(position) {
      if (this.currentElement) {
        if (this.currentElement.position == "订单池") {
          const currentStationOrders = this.stationOrdersArray[position];
          const len = currentStationOrders.length;
          if (currentStationOrders.indexOf(0) !== -1) {
            let currentElement = this.$d3.select(`#${this.currentElement.id}`);
            currentElement
              .transition()
              .duration(1000)
              .attr("y", "20")
              .transition()
              .duration(1000)
              .attr("x", "200")
              .remove()
              .on("end", () => {
                this.poolArray = this.poolArray.filter(
                  item => item.id !== this.currentElement.id
                );
                this.pathTransition(position);
              });
          } else {
            alert("工位wip已满，无法进栈");
          }
        } else {
          alert("衣架已在栈内！");
        }
        // currentElement
        // .transition()
        // .duration(1000)
        // .attr("x", "130")
        // .transition()
        // .duration(1000)
        // .attr("y", "0");
      } else {
        alert("请选择一个衣架！");
      }

      // demo
      //   .transition()
      //   .duration(1000)
      //   .ease(this.$d3.easeLinear)
      //   .attr("x", "130")
      //   .transition()
      //   .duration(1000)
      //   .ease(this.$d3.easeLinear)
      //   .attr("y", "0");
    },
    hangerOut(position) {
      const currentStation = this.stationArray[position];
      let currentStationOrders = this.stationOrdersArray[position];
      console.log(currentStationOrders);
      const wip = parseInt(currentStation.wip);
      this.currentElement = _.last(currentStationOrders);
      console.log(this.currentElement);
      if (this.currentElement.id) {
        let currentElement = this.$d3.select(`#${this.currentElement.id}`);
        currentElement
          .transition()
          .duration(1000)
          .attr("y", "20")
          .transition()
          .duration(1000)
          .attr("x", (2 + wip) * 30 + 200)
          .remove()
          .on("end", () => {
            this.currentElement.position = "仓库";
            let tempArray = _.initial(currentStationOrders);
            console.log(tempArray);
            tempArray.forEach(item => {
              if (item) {
                let element = this.$d3.select(`#${item.id}`);
                let oldX = parseInt(element.attr("x"));
                console.log(oldX);
                element
                  .transition()
                  .duration(500)
                  .attr("x", oldX + 30);
              }
            });
            console.log(tempArray);
            this.stationOrdersArray[position] = [0].concat(tempArray);
            this.currentElement = null;
          });
      } else {
        alert("无衣架可出栈！");
      }
    },
    setCurryRect(event) {
      console.log(event);
      // this.currentElement = this.$d3.select(`#${event.id}`);
      this.currentElement = event;
    },
    rotateSvg(position) {
      const currentStation = this.stationPosition[position];
      if (this.rotateFlag) {
        this.stationGroupArray[position]
          .transition()
          .duration(1000)
          .attr(
            "transform",
            `translate(${currentStation.origin[0]},${currentStation.origin[1]}) rotate(10)`
          );
      } else {
        this.stationGroupArray[position]
          .transition()
          .duration(1000)
          .attr(
            "transform",
            `translate(${currentStation.origin[0]},${currentStation.origin[1]})`
          );
      }
      this.rotateFlag = !this.rotateFlag;
    }
  }
};
</script>

<style lang="scss" scoped>
p {
  margin: 0 0;
  padding-left: 10px;
}
.container {
  .split-line {
    display: inline-block;
    margin: 0 10px;
  }

  button + button {
    margin-left: 10px;
  }

  width: 100%;
  height: 100vh;

  .svgcontainer {
    width: 100%;
    height: 400px;
  }

  .data-show {
    display: flex;
    text-align: left;

    .pool-data {
      width: 45%;
      height: 200px;
      border-right: 1px solid #000;
    }

    .station-data {
      width: 60%;
      height: 200px;

      .station-data-detail {
        display: flex;

        .detail {
          border-right: 1px solid #000;
          flex: 1;

          &:last-child {
            border-right: none;
          }
        }
      }
    }
  }

  .log {
    border-top: 1px solid #000;
    width: 100%;
    height: 300px;
    text-align: left;
  }
  i {
    display: inline-block;
    width: 20px;
    height: 12px;
  }
}
</style>