<template>
  <div class="container">
    <!-- <section>
      <button
        v-for="(item,index) in stationArray"
        :key="'push'+index+`${item.key}`"
        @click="hangerIn(index)"
      >衣架入栈 -> {{item.name}}</button>
      <span class="split-line">|</span>
      <button
        v-for="(item,index) in stationArray"
        :key="'out'+index+`${item.key}`"
        @click="hangerOut(index)"
      >{{item.name}} -> 衣架出栈</button>
      <span class="split-line">|</span>
      <button
        v-for="(item,index) in stationArray"
        :key="'spin'+index+`${item.key}`"
        @click="rotateSvg(index)"
      >旋转工位 -> {{item.name}}</button>
    </section>-->
    <div class="svgcontainer" id="svgcontainer"></div>
    <!-- <div class="data-show">
      <div class="pool-data">
        订单池数据:
        <p v-for="(item,index) in poolArray" :key="index">
          {
          <span>id: {{item.id}} ,</span>
          <span>
            color:
            <i :style="{background:item.color}"></i> ,
          </span>
          <span>position: {{item.position}}</span>
          }
        </p>
      </div>
      <div class="station-data">
        工位数据:
        <div class="station-data-detail">
          <div class="detail" v-for="(items,indexs) in stationOrdersArray" :key="indexs">
            {{stationArray[indexs].name}}:
            <p v-for="(item,index) in items" :key="index">
              <template v-if="item">
                {
                <span>id: {{item.id}} ,</span>
                <span>
                  color:
                  <i :style="{background:item.color}"></i> ,
                </span>
                <span>position: {{item.position}}</span>
                }
              </template>
              <template v-else>{{item}}</template>
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="log">
      数据源:
      <p v-for="(item,index) in clothingData" :key="index">
        {
        <span>id: {{item.id}} ,</span>
        <span>
          color:
          <i :style="{background:item.color}"></i> ,
        </span>
        <span>position: {{item.position}}</span>
        }
      </p>
    </div>-->
  </div>
</template>

<script>
import { mapState } from "vuex";
import _ from "lodash";

export default {
  data() {
    return {
      poolArray: [],
      stationArray: [],
      svg: null,
      lineGroup: null,
      lineGroupArray: [],
      pathArray: [],
      hangerPool: null,
      stationGroupArray: [],
      stationOrders: [],
      stationOrdersArray: [],
      currentElement: null,
      clothingData: [],
      rotateFlag: true,
      stationWip: 4,
      stationPosition: [],
      dataSourcePosition: [],
    };
  },
  computed: {
    ...mapState(["plotData"]),
  },
  mounted() {
    this.svgInit();
  },
  methods: {
    svgInit() {
      const width = `100%`;
      const height = `100%`;
      this.svg = this.$d3
        .select("#svgcontainer")
        .append("svg")
        .attr("width", width)
        .attr("height", height);

      //创建轨道
      this.createPath();

      //模拟数据1，创建工位
      const staticData1 = [];
      for (let i = 0; i < 10; i++) {
        staticData1.push({
          text: `工位${i}`,
          type: "station",
          name: `工位${i}`,
          wip: 5,
          key: `station${i}`,
          link: "middleTop",
          position: "top",
        });
      }
      this.createStation(staticData1);

      //模拟数据2，创建工位
      const staticData2 = [];
      for (let i = 0; i < 10; i++) {
        staticData2.push({
          text: `工位${i}`,
          type: "station",
          name: `工位${i}`,
          wip: 5,
          key: `station${i}`,
          link: "middleTop",
          position: "bottom",
        });
      }
      this.createStation(staticData2);

      //模拟数据3，创建工位
      const staticData3 = [];
      for (let i = 0; i < 10; i++) {
        staticData3.push({
          text: `工位${i}`,
          type: "station",
          name: `工位${i}`,
          wip: 5,
          key: `station${i}`,
          link: "middleBottom",
          position: "top",
        });
      }
      this.createStation(staticData3);

      //模拟数据4，创建工位
      const staticData4 = [];
      for (let i = 0; i < 10; i++) {
        staticData4.push({
          text: `工位${i}`,
          type: "station",
          name: `工位${i}`,
          wip: 5,
          key: `station${i}`,
          link: "middleBottom",
          position: "bottom",
        });
      }
      this.createStation(staticData4);

      //创建存储区
      this.createStorageArea();

      //创建筛选区
      this.createFilterArea();

      //创建配对站
      this.createPairingStation();
    },
    createStorageArea() {
      let that = this;

      let lineGroup = that.svg
        .append("g")
        .attr("transform", `translate(1090, 80)`);

      //插入背景
      lineGroup
        .append("rect")
        .attr("x", 0)
        .attr("y", 0)
        .attr("width", 690)
        .attr("height", 400)
        .attr("fill", "#011d34")
        .style("stroke", "none");

      //插入文本;
      lineGroup
        .append("text")
        .attr("x", 15)
        .attr("y", 200)
        .attr("font-size", 20)
        .attr("fill", "#00FFFF")
        .text(`存储区`);

      var line = that.$d3.line().curve(that.$d3.curveStep);

      for (let i = 0; i < 16; i++) {
        let stackGroup = lineGroup
          .append("g")
          .attr("transform", `translate(${85 + 35 * i}, 0)`);

        var lineData = [
          [15, 0],
          [15, 400],
        ];

        let tempLine = stackGroup
          .append("path")
          .attr("d", line(lineData))
          .style("stroke", "#00FFFF")
          .style("fill", "none");

        stackGroup
          .append("rect")
          .attr("x", 0)
          .attr("y", 15)
          .attr("width", 30)
          .attr("height", 15)
          .attr("fill", "#000317")
          .style("stroke", "#00FFFF")
          .style("stroke-width", 1);

        stackGroup
          .append("text")
          .attr("x", 10)
          .attr("y", 27)
          .attr("font-size", 12)
          .attr("fill", "#FFFFFF")
          .text(`${i + 1}`);
      }
    },
    createFilterArea() {
      let that = this;

      let lineGroup = that.svg
        .append("g")
        .attr("transform", `translate(1090, 520)`);

      lineGroup
        .append("rect")
        .attr("x", 0)
        .attr("y", 0)
        .attr("width", 690)
        .attr("height", 170)
        .attr("fill", "#011d34")
        .style("stroke", "none");

      //插入文本;
      lineGroup
        .append("text")
        .attr("x", 15)
        .attr("y", 85)
        .attr("font-size", 20)
        .attr("fill", "#00FFFF")
        .text(`筛选区`);

      var line = that.$d3.line().curve(that.$d3.curveStep);

      for (let i = 0; i < 8; i++) {
        let stackGroup = lineGroup
          .append("g")
          .attr("transform", `translate(${85 + 35 * i}, 0)`);

        var lineData = [
          [15, 0],
          [15, 170],
        ];

        let tempLine = stackGroup
          .append("path")
          .attr("d", line(lineData))
          .style("stroke", "#00FFFF")
          .style("fill", "none");

        stackGroup
          .append("rect")
          .attr("x", 0)
          .attr("y", 15)
          .attr("width", 30)
          .attr("height", 15)
          .attr("fill", "#000317")
          .style("stroke", "#00FFFF")
          .style("stroke-width", 1);

        stackGroup
          .append("text")
          .attr("x", 10)
          .attr("y", 27)
          .attr("font-size", 12)
          .attr("fill", "#FFFFFF")
          .text(`${i + 1}`);
      }
    },
    createPath() {
      const staticData = [
        {
          text: "轨道1",
          type: "line",
          name: "轨道1",
          key: "line1",
          direction: "column",
          position: "left",
        },
        {
          text: "轨道2",
          type: "line",
          name: "轨道2",
          key: "line2",
          direction: "row",
          showName: true,
          position: "middleTop",
        },
        {
          text: "轨道3",
          type: "line",
          name: "轨道3",
          key: "line3",
          direction: "column",
          position: "right",
        },
        {
          text: "轨道4",
          type: "line",
          name: "轨道4",
          key: "line4",
          showName: true,
          direction: "row",
          position: "middleBottom",
        },
        {
          text: "轨道5",
          type: "line",
          name: "轨道5",
          key: "line5",
          direction: "row",
          position: "connectionTop",
        },
        {
          text: "轨道6",
          type: "line",
          name: "轨道6",
          key: "line6",
          direction: "row",
          position: "connectionBottom",
        },
        {
          text: "轨道7",
          type: "line",
          name: "轨道7",
          key: "line7",
          direction: "column",
          position: "rightLeft",
        },
        {
          text: "轨道8",
          type: "line",
          name: "轨道8",
          key: "line8",
          showName: true,
          direction: "row",
          position: "rightTop",
        },
        {
          text: "轨道9",
          type: "line",
          name: "轨道9",
          key: "line9",
          showName: true,
          direction: "row",
          position: "rightMiddle",
        },
        {
          text: "轨道10",
          type: "line",
          name: "轨道10",
          key: "line10",
          showName: true,
          direction: "row",
          position: "rightBottom",
        },
        {
          text: "轨道11",
          type: "line",
          name: "轨道11",
          key: "line11",
          direction: "column",
          position: "rightRight",
        },
      ];

      let that = this;

      var line = that.$d3.line().curve(that.$d3.curveStep);
      var lineData = {
        left: {
          transform: [0, 0],
          data: [
            [0, 0],
            [40, 0],
            [40, 850],
            [0, 850],
            [0, 0],
          ],
        },
        middleTop: {
          transform: [40, 230],
          data: [
            [0, 0],
            [910, 0],
            [910, 40],
            [0, 40],
            [0, 0],
          ],
        },
        middleBottom: {
          transform: [40, 580],
          data: [
            [0, 0],
            [910, 0],
            [910, 40],
            [0, 40],
            [0, 0],
          ],
        },
        right: {
          transform: [950, 0],
          data: [
            [0, 0],
            [40, 0],
            [40, 850],
            [0, 850],
            [0, 0],
          ],
        },
        connectionTop: {
          transform: [990, 500],
          data: [
            [0, 0],
            [60, 0],
            [60, 30],
            [0, 30],
            [0, 0],
          ],
        },
        connectionBottom: {
          transform: [990, 580],
          data: [
            [0, 0],
            [60, 0],
            [60, 30],
            [0, 30],
            [0, 0],
          ],
        },
        rightLeft: {
          transform: [1050, 0],
          data: [
            [0, 0],
            [40, 0],
            [40, 850],
            [0, 850],
            [0, 0],
          ],
        },
        rightTop: {
          transform: [1090, 40],
          data: [
            [0, 0],
            [690, 0],
            [690, 40],
            [0, 40],
            [0, 0],
          ],
        },
        rightMiddle: {
          transform: [1090, 480],
          data: [
            [0, 0],
            [690, 0],
            [690, 40],
            [0, 40],
            [0, 0],
          ],
        },
        rightBottom: {
          transform: [1090, 690],
          data: [
            [0, 0],
            [690, 0],
            [690, 40],
            [0, 40],
            [0, 0],
          ],
        },
        rightRight: {
          transform: [1780, 0],
          data: [
            [0, 0],
            [40, 0],
            [40, 850],
            [0, 850],
            [0, 0],
          ],
        },
      };
      staticData.forEach((item, index) => {
        // 插入基本组件轨道示例;
        let lineGroup = that.svg
          .append("g")
          .attr(
            "transform",
            `translate(${lineData[item.position].transform[0]}, ${
              lineData[item.position].transform[1]
            })`
          );
        let tempLine = lineGroup
          .append("path")
          .attr("d", line(lineData[item.position].data))
          .style("stroke", "#00FFFF")
          .style("fill", "none");

        //插入文本;
        if (item.showName && item.direction === "row") {
          lineGroup
            .append("text")
            .attr("x", lineData[item.position].data[1][0] / 2)
            .attr("y", 25)
            .attr("font-size", 20)
            .attr("fill", "#00FFFF")
            .text(`${item.name}`);
        }

        that.pathArray.push(tempLine);
        that.lineGroupArray.push(lineGroup);
      });
    },
    createPairingStation() {
      const staticData = [
        {
          text: "配对站1",
          type: "pairing",
          name: "配对站1",
          wip: 5,
          key: "pairing1",
        },
        {
          text: "配对站2",
          type: "pairing",
          name: "配对站2",
          wip: 5,
          key: "pairing2",
        },
      ];

      const drawValue = [
        { x: 70, y: 0 },
        { x: 70, y: 100 },
        { x: 0, y: 100 },
        { x: 0, y: 80 },
        { x: 0, y: 30 },
        { x: 0, y: 0 },
        { x: -15, y: 30 },
        { x: 20, y: 15 },
      ];

      staticData.forEach((item, index) => {
        const wip = parseInt(item.wip);
        let stationGroup = this.svg
          .append("g")
          .attr("transform", `translate(${110 * index + 1230}, 730)`);

        //插入进站线路
        stationGroup
          .append("line")
          .attr("x1", drawValue[0].x)
          .attr("y1", drawValue[0].y)
          .attr("x2", drawValue[1].x)
          .attr("y2", drawValue[1].y)
          .style("stroke", "#00FFFF")
          .style("stroke-width", 1);
        stationGroup
          .append("line")
          .attr("x1", drawValue[1].x)
          .attr("y1", drawValue[1].y)
          .attr("x2", drawValue[2].x)
          .attr("y2", drawValue[2].y)
          .style("stroke", "#00FFFF")
          .style("stroke-width", 1);
        stationGroup
          .append("line")
          .attr("x1", drawValue[2].x)
          .attr("y1", drawValue[2].y)
          .attr("x2", drawValue[3].x)
          .attr("y2", drawValue[3].y)
          .style("stroke", "#00FFFF")
          .style("stroke-width", 1);
        stationGroup
          .append("line")
          .attr("x1", drawValue[4].x)
          .attr("y1", drawValue[4].y)
          .attr("x2", drawValue[5].x)
          .attr("y2", drawValue[5].y)
          .style("stroke", "#00FFFF")
          .style("stroke-width", 1);

        //插入wip分组
        let wipGroup = stationGroup.append("g");

        wipGroup
          .append("rect")
          .attr("x", drawValue[6].x)
          .attr("y", drawValue[6].y)
          .attr("width", 30)
          .attr("height", 50)
          .attr("fill", "none")
          .style("stroke", "#00FFFF")
          .style("stroke-width", 1);

        for (let i = 0; i < wip; i++) {
          wipGroup
            .append("rect")
            .attr("x", drawValue[6].x)
            .attr("y", i * 10 + drawValue[6].y)
            .attr("width", 30)
            .attr("height", 10)
            .attr("fill", "none")
            .style("stroke", "#00FFFF")
            .style("stroke-width", 1);
        }

        //插入文本;
        wipGroup
          .append("text")
          .attr("x", drawValue[7].x)
          .attr("y", drawValue[7].y)
          .attr("font-size", 10)
          .attr("fill", "#FFFFFF")
          .text(`${item.name}`);
      });
    },
    createStation(data) {
      const lineMap = {
        middleToptop: [65, 130],
        middleTopbottom: [65, 270],
        middleBottomtop: [65, 480],
        middleBottombottom: [65, 620],
      };
      const drawMap = {
        top: [
          { x: 0, y: 100 },
          { x: 0, y: 0 },
          { x: 50, y: 0 },
          { x: 50, y: 20 },
          { x: 50, y: 70 },
          { x: 50, y: 100 },
          { x: 35, y: 20 },
          { x: 10, y: 95 },
        ],
        bottom: [
          { x: 50, y: 0 },
          { x: 50, y: 100 },
          { x: 0, y: 100 },
          { x: 0, y: 80 },
          { x: 0, y: 30 },
          { x: 0, y: 0 },
          { x: -15, y: 30 },
          { x: 10, y: 15 },
        ],
      };
      const staticData = data;
      // for (let i = 0; i < 10; i++) {
      //   staticData.push({
      //     text: `工位${i}`,
      //     type: "station",
      //     name: `工位${i}`,
      //     wip: 5,
      //     key: `station${i}`,
      //     link: "middleTop",
      //     position: "top",
      //   });
      // }
      staticData.forEach((item, index) => {
        const lineValue = lineMap[`${item.link}${item.position}`];
        const drawValue = drawMap[`${item.position}`];

        const wip = parseInt(item.wip);
        let stationGroup = this.svg
          .append("g")
          .attr(
            "transform",
            `translate(${90 * index + lineValue[0]}, ${lineValue[1]})`
          );

        //插入进站线路
        stationGroup
          .append("line")
          .attr("x1", drawValue[0].x)
          .attr("y1", drawValue[0].y)
          .attr("x2", drawValue[1].x)
          .attr("y2", drawValue[1].y)
          .style("stroke", "#00FFFF")
          .style("stroke-width", 1);
        stationGroup
          .append("line")
          .attr("x1", drawValue[1].x)
          .attr("y1", drawValue[1].y)
          .attr("x2", drawValue[2].x)
          .attr("y2", drawValue[2].y)
          .style("stroke", "#00FFFF")
          .style("stroke-width", 1);
        stationGroup
          .append("line")
          .attr("x1", drawValue[2].x)
          .attr("y1", drawValue[2].y)
          .attr("x2", drawValue[3].x)
          .attr("y2", drawValue[3].y)
          .style("stroke", "#00FFFF")
          .style("stroke-width", 1);
        stationGroup
          .append("line")
          .attr("x1", drawValue[4].x)
          .attr("y1", drawValue[4].y)
          .attr("x2", drawValue[5].x)
          .attr("y2", drawValue[5].y)
          .style("stroke", "#00FFFF")
          .style("stroke-width", 1);

        //插入wip分组
        let wipGroup = stationGroup.append("g");

        wipGroup
          .append("rect")
          .attr("x", drawValue[6].x)
          .attr("y", drawValue[6].y)
          .attr("width", 30)
          .attr("height", 50)
          .attr("fill", "none")
          .style("stroke", "#00FFFF")
          .style("stroke-width", 1);

        let tempArray = [];
        for (let i = 0; i < wip; i++) {
          wipGroup
            .append("rect")
            .attr("x", drawValue[6].x)
            .attr("y", i * 10 + drawValue[6].y)
            .attr("width", 30)
            .attr("height", 10)
            .attr("fill", "none")
            .style("stroke", "#00FFFF")
            .style("stroke-width", 1);
          tempArray.push(0);
        }
        this.stationOrdersArray.push(tempArray);

        //插入文本;
        wipGroup
          .append("text")
          .attr("x", drawValue[7].x)
          .attr("y", drawValue[7].y)
          .attr("font-size", 10)
          .attr("fill", "#FFFFFF")
          .text(`${item.name}`);

        this.stationGroupArray.push(stationGroup);
      });
    },
  },
};
</script>

<style lang="scss" scoped>
p {
  margin: 0 0;
  padding-left: 10px;
}
.container {
  width: 1920px;
  height: 1080px;
  padding: 180px 50px 50px 50px;
  background: #000317;

  .split-line {
    display: inline-block;
    margin: 0 10px;
  }

  button + button {
    margin-left: 10px;
  }

  .svgcontainer {
    width: 100%;
    height: 100%;
  }

  .data-show {
    display: flex;
    text-align: left;

    .pool-data {
      width: 45%;
      height: 200px;
      border-right: 1px solid #000;
    }

    .station-data {
      width: 60%;
      height: 200px;

      .station-data-detail {
        display: flex;

        .detail {
          border-right: 1px solid #000;
          flex: 1;

          &:last-child {
            border-right: none;
          }
        }
      }
    }
  }

  .log {
    border-top: 1px solid #000;
    width: 100%;
    height: 300px;
    text-align: left;
  }
  i {
    display: inline-block;
    width: 20px;
    height: 12px;
  }
}
</style>