<template>
  <div class="about">
    <h1>This is an about page</h1>
    <button @click="format(tempData)">转换</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tempData: [
        {
          a: "xiao ming",
          b: "18",
          c: [
            {
              a: "Zootopia",
              b: 109,
              c: "tt2948356"
            },
            {
              a: "Hacksaw Ridge",
              b: 139,
              c: "tt2119532"
            },
            {
              a: "Kiminona",
              b: 106,
              c: "tt5311514"
            }
          ]
        },
        {
          a: "xiao ming",
          b: "18",
          c: [
            {
              a: "Zootopia",
              b: 109,
              c: "tt2948356"
            },
            {
              a: "Hacksaw Ridge",
              b: 139,
              c: "tt2119532"
            },
            {
              a: "Kiminona",
              b: 106,
              c: "tt5311514"
            }
          ]
        }
      ],
      attrMap: [
        {
          a: "name",
          b: "age",
          c: "favoriteMovies"
        },
        {
          a: "name",
          b: "duration",
          c: "IMDb"
        }
      ]
    };
  },
  methods: {
    format(data) {
      if (typeof data === "object" && data instanceof Array) {
        var len = data.length;
        if (len !== 0) {
          var returnValue = [];
          for (var i = 0; i < len; i++) {
            var obj = data[i];
            var formatObj = {};
            for (var prop in obj) {
              // console.log("obj." + prop + " = " + obj[prop]);
              if (typeof obj[prop] === "object" && obj[prop] instanceof Array) {
                formatObj[this.attrMap[0][prop]] = this.format(obj[prop]);
              } else {
                formatObj[this.attrMap[0][prop]] = obj[prop];
              }
            }
            returnValue.push(formatObj);
          }
          console.log(returnValue);
          return returnValue;
        } else {
          console.warn("Empty data.");
        }
      } else {
        console.error("Wrong data format.");
      }
    }
  }
};
</script>
